import React from 'react'
import './Navbar.css';
import Logo from '../Images/Logo.png';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faToggleOff } from '@fortawesome/free-solid-svg-icons';

const Navbar = () => {
    document.addEventListener('DOMContentLoaded', function() {
        var menuItems = document.querySelectorAll('.menu > li');
  
        menuItems.forEach(function(menuItem) {
          menuItem.addEventListener('mouseenter', function() {
            var submenu = this.querySelector('.submenu');
            if (submenu) {
              submenu.style.width = this.offsetWidth + 'px';
            }
          });
        });
      });
  return (
    <div>
        <div className='d-none d-lg-block'>
            <div className='Header'>
                <div className='Navigation'>
                    <nav>
                        <ul className='menu'>
                            <li><a href="#home">Home</a></li>
                            <li><a href="#about">About Us</a></li>
                            <li class="dynamic-width">
                                <a href="#categories">Categories</a>
                                <ul className='submenu'>
                                    <li><a href="">Off-Grid</a></li>
                                    <li><a href="">On-Grid</a></li>
                                    <li><a href=''>Hybrid</a></li>
                                </ul>
                            </li>
                            <li><a href="#contact">Contact</a></li>                        
                        </ul>
                    </nav>
                    <div className="LogoImage">
                        <img src={Logo}/>
                    </div>
                    <div className="QuickChatButtonFrame">
                        <button className='btn' href="#chat">Quick Chat</button>
                    </div>
                </div>
                <hr/>
            </div>
        </div>
        <div className='d-block d-lg-none'>
            <div className='Navbar-Mobile'>
                <div className="LogoImage">
                    <img src={Logo}/>
                </div>
                <div className='ToggleButton-Mobile'>
                    <FontAwesomeIcon icon={faToggleOff} />
                </div>
            </div>
        </div>
    </div>
  )
}

export default Navbar