import React, { useState } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEnvelope, faMapMarker, faPhone} from '@fortawesome/free-solid-svg-icons';
import { faFacebook, faInstagram, faTwitter } from '@fortawesome/free-brands-svg-icons'; 
import './Contact.css'

const Contact = () => {
  const [clicked, setclicked] = useState(false);
  const [value, setValue] = useState(null);
 
  const Address = () => {
      setclicked(true);
      let address = {
        heading : 'Address',
        details : 'Jaffar Sarang st, Parrys, Chennai - 600001.' 
      }
      setValue(address);
  }
  const PhoneNumber = () => {
      let phonenumber = {
        heading : 'Phone Number',
        details : '+91 98845 67977'
      }
      setclicked(true);
      setValue(phonenumber);
  }
  const Email = () => {
      let email = {
        heading :  'Email',
        details : 'ezsolar.sales@gmail.com'
      }
      setclicked(true);
      setValue(email);
  }

  return (
    <div>
      <div className='d-none d-lg-block'>
        <div className='d-flex flex-row'>
          <div className='col-5 Contact-Details-Desktop'>
            <div className='Welcome-Note'>
              <text>Get in Touch!</text>
            </div>
            <div className='Details-Container'>
              <div className='Details-Container-Icon wrapper'>
                <div className='icon Address'>
                  <span className="tooltip">Address</span>
                  <FontAwesomeIcon className='Icon-Style-Desktop' icon={faMapMarker}/>
                </div>
                <div className='Icon-Details-Desktop'>
                  <h3>Address</h3>
                  <text>Jaffar Sarang st, Parrys, Chennai - 600001.</text>
                </div>
              </div>
              <hr/>
              <div className='Details-Container-Icon wrapper'>
                <div className='icon Phone-Number'>
                  <span className="tooltip">Phone Number</span>
                  <FontAwesomeIcon className='Icon-Style-Desktop' icon={faPhone}/>
                </div>
                <div className='Icon-Details-Desktop'>
                  <h3>Phone Number</h3>
                  <text>+91 98845 67977</text>
                </div>
              </div>
              <hr/>
              <div className='Details-Container-Icon wrapper'>
                <div className='icon Email'>
                  <span className="tooltip">Email</span>
                  <FontAwesomeIcon className='Icon-Style-Desktop' icon={faEnvelope}/>
                </div>
                <div className='Icon-Details-Desktop'>
                  <h3>Email</h3>
                  <text>ezsolar.sales@gmail.com</text>
                </div>
              </div>
            </div>
          </div>
          <hr class="vertical-line"/>
          <div className='col-7 Contact-Form-Desktop'>
              <div className='Form-Text'>
                <text>Ready to join the EZ Solar movement?</text>
              </div>
              <form method="post" action="{{ url_for('submit_form') }}">
                <div className="d-flex form-group-signup">
                  <div className='Input-Elements d-flex flex-column'>
                    <label>Name</label>
                    <input type='text' placeholder='Fullname'/>
                  </div>
                  <div className='Input-Elements d-flex flex-column'>
                    <label>Phone Number</label>
                    <input type='tel' maxlength="10" placeholder='+91 98845 67977'/>
                  </div>
                </div>
                <div className='Input-Elements Desktop d-flex flex-column'>
                  <label>Email</label>
                  <input  type='email' placeholder='ezsolar.sales@gmail.com'/>
                </div>
                <div className="Message-Box-Desktop Desktop d-flex flex-column">
                  <label>Send message</label>
                  <textarea type="text" id="message" placeholder='Give your feedback!'/>
                </div>
              </form>
              <div className='d-flex justify-content-center'>
                <button className='btn Submit-Button-Desktop'>Send Message</button>
              </div>
          </div>
        </div>
        <div className='Footer-Desktop'>
            <text>
              Contact us today for a free consultation and let's 
              discuss how we can help you harness the power of the
              sun to create a brighter future for your home and our planet.
            </text>
        </div>
        <div className='Footer-Contact'>
          <div className='Social-Media-Icon'>
            <ul className='wrapper'>
              <li class="icon Instagram">
                <span class="tooltip">Instagram</span>
                <FontAwesomeIcon className='Icon-Style' icon={faInstagram} />
              </li>
              <li class="icon Twitter">
                <span class="tooltip">Twitter</span>
                <FontAwesomeIcon className='Icon-Style' icon={faTwitter} />
              </li>
              <li class="icon Facebook">
                <span class="tooltip">Facebook</span>
                <FontAwesomeIcon className='Icon-Style' icon={faFacebook} />
              </li>
            </ul>
          </div>
          <div className='Text-Footer'>
            <text>Together, let's turn up the sunshine!</text>
          </div>
        </div>
      </div>
      <div className='d-block d-lg-none'>
        <div className='Icon-Container'>
          <ul className='wrapper'>
            <li className="icon Address" onClick={Address}>
              <span className="tooltip">Address</span>
              <FontAwesomeIcon className='Icon-Style' icon={faMapMarker} />
            </li>
            <li className="icon Phone-Number" onClick={PhoneNumber}>
              <span className="tooltip">Phone Number</span>
              <FontAwesomeIcon className='Icon-Style' icon={faPhone} />
            </li>
            <li className="icon Email" onClick={Email}>
              <span className="tooltip">Email</span>
              <FontAwesomeIcon className='Icon-Style' icon={faEnvelope} />
            </li>
          </ul>
        </div>
        { clicked && (
          <div className='Icon-Content-Container d-flex flex-row'>
              <div className='Contact-Heading col-6'>
                  <text>{value.heading}</text>
              </div>
              <div className='Contact-Details col-6'>
                  <text>{value.details}</text>
              </div>   
          </div>
        )}
        <div className='d-flex justify-content-center'>
          <div className='Form-Container' method="post" action="{{ url_for('submit_form') }}">
            <div className='Text-Form'>
              <text>Ready to join the EZ Solar movement?</text>
            </div>
            <div className='Input-Elements d-flex flex-column'>
              <label>Name</label>
              <input type='text' placeholder='Fullname'/>
            </div>
            <div className='Input-Elements d-flex flex-column'>
              <label>Email</label>
              <input type='email' placeholder='ezsolar.sales@gmail.com'/>
            </div>
            <div className='Input-Elements d-flex flex-column'>
              <label>Phone Number</label>
              <input type='tel' maxlength="10" placeholder='+91 98845 67977'/>
            </div>
            <div className='Input-Elements d-flex flex-column'>
              <label>Location</label>
              <input type='text' placeholder='Chennai'/>
            </div>
            <div className='Message-Box d-flex flex-column'>
              <label>Send Message</label>
              <textarea type='text' placeholder='Give your feedback!' />
            </div>
            <div>
              <button className='btn Submit-Button'>Send</button>
            </div>
          </div>
        </div>
        <div className='Footer-Contact'>
          <div className='Social-Media-Icon'>
            <ul className='wrapper'>
              <li class="icon Instagram">
                <span class="tooltip">Instagram</span>
                <FontAwesomeIcon className='Icon-Style' icon={faInstagram} />
              </li>
              <li class="icon Twitter">
                <span class="tooltip">Twitter</span>
                <FontAwesomeIcon className='Icon-Style' icon={faTwitter} />
              </li>
              <li class="icon Facebook">
                <span class="tooltip">Facebook</span>
                <FontAwesomeIcon className='Icon-Style' icon={faFacebook} />
              </li>
            </ul>
          </div>
          <div className='Text-Footer'>
            <text>Together, let's turn up the sunshine!</text>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Contact